    /*body styles */
    body {
        margin: 0;
        overflow-y: hidden;
        font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
        -webkit-tap-highlight-color: transparent;
    }
    /*end of body styles */

    #header {
        &.header-section, .search-icon {
            margin: 0;
            height: 50px;
        }
  
        #hamburger.icon-menu {
            font-size: 24px;
            float: left;
            line-height: 50px;
        }
  
        .right-header {
            height: 35px;
            padding: 7px;
            float: right;
        }
  
        .list {
            list-style: none;
            cursor: pointer;
            font-size: 16px;
            line-height: 35px;
        }
  
        .header-list {
            padding-left: 15px;
            margin: 0;
        }
  
        .search-icon {
            float: left;
            padding-left: 15px;
            border: 0px solid #0a9444 !important;
            background-color: #0a9444;
            cursor: text;
            width: 10em;
        
            &:focus {
                outline: none;
                cursor: default;
            }
        }
    }
  
    #maintext.main-content {
        height: 100vh;
        z-index: 1000;
    }
  
    #header {
        .header-list .horizontal-menu .e-menu-item {
            height: 35px;
            vertical-align: middle;
            font-size: 16px;
            line-height: 35px;
        }
    
        .e-menu-item .e-caret {
            line-height: 35px;
        }
    }

    #header .icon-menu:before {
        content: '\e812';
    }

    .main-content {
        background-color: rgba(160, 160, 165, 0.16);
    }
  
    .sidebar-menu {
        font-family: 'fontello';
        width: 220px;
    
        .e-menu-wrapper ul .e-menu-item {
            &.e-menu-caret-icon {
                width: 220px;
            }
    
            &:hover {
                background-color: #0a9444 !important;
            }
        }
    }
  
    .e-menu-wrapper ul .e-menu-item {
        &.e-focused:hover, &.e-selected {
            background-color: #0a9444 !important;
        }
    
        .e-caret {
            color: #0a9444 !important;
        }
    }
  
    #header .search-icon, .sidebar-menu .action-btn, #header .e-menu-item .e-caret, .e-menu-wrapper ul .e-menu-item {
        color: #0a9444 !important;
    }
  
    .e-close {
        .e-menu-wrapper ul .e-menu-item {
            width: 50px;
        }
    
        ul .e-menu-item.e-menu-caret-icon {
            padding-right: 12px;
        }
    }

    .sidebar-menu.e-dock.e-close {
        .e-menu-wrapper ul .e-menu-item .e-caret, .main-menu-header, .action-btn {
            display: none;
        }
    
        .e-menu-wrapper ul {
            .e-menu-item.e-menu-caret-icon, &.e-vertical {
                min-width: 0;
                width: 50px !important;
            }
        
            &.e-menu {
                font-size: 0;
            }
        }
    
        .e-menu-item .e-menu-icon {
            font-size: 20px;
            padding: 0;
        }
    }

    .e-menu-wrapper ul .e-menu-item.e-focused {
        background-color: #0a9444 !important;
    }
  
    .sidebar-menu, #header ul {
        background-color: #bdeba7 !important;
        color: #0a9444 !important;
        overflow: hidden;
    }
    
    .e-menu-wrapper {
        background-color: #bdeba7 !important;
        color: #0a9444 !important;
        overflow: hidden;
    
        ul {
            background-color: #bdeba7 !important;
            color: #0a9444 !important;
            overflow: hidden;
        }
    }
    .sidebar-menu {

        
        margin-left: -1px;
    
        .main-menu-header {
            padding: 4px 0 0 18px;
            color: #656a70;
        }
    
        .action-btn {
            background-color: #1784c7;
            margin-left: 16px;
            width: 165px;
            height: 30px;
            font-size: 13px;
            border-radius: 5px;
        }
    }
  
    .center {
        text-align: center;
        display: none;
        font-size: 13px;
        font-weight: 400;
        margin-top: 20px;
    }
    
    .sb-content-tab .center {
        display: block;
    }
    
    #sb-content-header {
        display: none;
    }
    
    .sb-content-section {
        border: 0;
    }
    
    .col-md-12, body {
        padding: 0;
    }
  
    .sidebar-menu {
        .icon-up-hand:before {
            content: '\e801';
        }
    
        .icon-bell-alt:before {
            content: '\e802';
        }
    
        .icon-user:before {
            content: '\e803';
        }
    
        .icon-picture:before {
            content: '\e804';
        }
    
        .icon-comment-alt:before {
            content: '\e805';
        }
    
        .icon-comment-inv-alt2:before {
            content: '\e807';
        }
    
        .icon-tag:before {
            content: '\e806';
        }
    
        .icon-eye:before {
            content: '\e808';
        }
    
        .icon-globe:before {
            content: '\e809';
        }
    
        .icon-bookmark-empty:before {
            content: '\e810';
        }
    
        .icon-bookmark:before {
            content: '\e811';
        }
    }

    .sidebar-menu .icon, #header #hamburger.icon-menu {
        font-family: 'fontello';
    }

    .sidebar-menu .e-menu-icon::before {
        color: #656a70;
    }

    .sf-new {
        .sb-header, .sb-bread-crumb, #action-description, .sb-action-description, .e-tab-header, 
        .description-section, #description-section, #description, #navigation-btn, .sb-toolbar-splitter, 
        .sb-footer, #left-sidebar {
            display: none;
        }
    }

    .sb-component-name {
        display: none;
    }

    .sf-new .sb-right-pane.e-view {
        margin-left: 0px !important;
    }

    .sb-action-description.sb-rightpane-padding {
        padding-bottom: 0;
    }

    .description-section {
        padding-top: 0;
    }

    #content-tab.sb-content-tab {
        height: 100% !important;
    }

    .sf-new .container-fluid {
        padding: 0;

        .control-section {
            padding: 0;
        }
    }

    #sidebar-section, description-section sb-rightpane-padding {
        padding: 0;
    }

    .sb-component-name.sb-rightpane-padding {
        margin-top: -56px;
    }

    .sb-right-pane.e-view {
        left: 0;
        padding-left: 0;
        padding-right: 0;
        top: 0;
        overflow-y: hidden;
    }

    .sb-desktop-wrapper {
        height: 100%;
    }

    .sb-component-name h1 {
        padding-top: 0;
    }

    .sf-new .sb-content.e-view {
        top: 0;
    }

    #blazor-error-ui {
        background: lightyellow;
        bottom: 0;
        box-shadow: 0 -1px 2px rgba(0, 0, 0, 0.2);
        display: none;
        left: 0;
        padding: 0.6rem 1.25rem 0.7rem 1.25rem;
        position: fixed;
        width: 100%;
        z-index: 1000;

        .dismiss {
            cursor: pointer;
            position: absolute;
            right: 0.75rem;
            top: 0.5rem;
        }
    }